ght: 200px), (max-width: 130px) and (min-height: 100px) and (max-height: 200px), (max-width: 195px) and (min-height: 150px) and (max-height: 200px), (max-width: 260px) and (min-height: 200px) and (max-height: 200px){.adContainer .logo{display:none}.adContainer .text{margin:10px auto auto auto}}

